import React from 'react';
import {
  Box,
  FormControl,
  Select,
  MenuItem,
  SelectChangeEvent,
  InputLabel,
  Chip,
  Typography,
} from '@mui/material';
import { Language, Flag } from '@mui/icons-material';

interface LanguageSelectorProps {
  language: 'arabic' | 'english';
  onLanguageChange: (language: 'arabic' | 'english') => void;
}

const LanguageSelector: React.FC<LanguageSelectorProps> = ({
  language,
  onLanguageChange,
}) => {
  const handleChange = (event: SelectChangeEvent<string>) => {
    onLanguageChange(event.target.value as 'arabic' | 'english');
  };

  const languages = [
    { value: 'arabic', label: '🇴🇲 العربية', nativeLabel: 'العربية العمانية' },
    { value: 'english', label: '🇬🇧 English', nativeLabel: 'English' },
  ];

  return (
    <Box sx={{ minWidth: 150 }}>
      <FormControl size="small" fullWidth>
        <Select
          value={language}
          onChange={handleChange}
          sx={{
            color: 'white',
            '& .MuiOutlinedInput-notchedOutline': {
              borderColor: 'rgba(255, 255, 255, 0.3)',
            },
            '&:hover .MuiOutlinedInput-notchedOutline': {
              borderColor: 'rgba(255, 255, 255, 0.5)',
            },
            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderColor: 'rgba(255, 255, 255, 0.7)',
            },
            '& .MuiSvgIcon-root': {
              color: 'white',
            },
          }}
          displayEmpty
          renderValue={(selected: unknown) => {
            const selectedLang = languages.find(lang => lang.value === selected);
            return selectedLang?.label || '';
          }}
        >
          {languages.map((lang) => (
            <MenuItem key={lang.value} value={lang.value}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Typography component="span">{lang.label}</Typography>
                <Chip
                  label={lang.nativeLabel}
                  size="small"
                  variant="outlined"
                  sx={{ ml: 1 }}
                />
              </Box>
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Box>
  );
};

export default LanguageSelector;
