import React, { useState, useRef, useEffect } from 'react';
import { Theme } from '@mui/material/styles';
import {
  Box,
  Paper,
  Typography,
  TextField,
  IconButton,
  Avatar,
  Chip,
  CircularProgress,
  Fade,
  Divider,
} from '@mui/material';
import { Send, Person, SmartToy, VolumeUp } from '@mui/icons-material';
import { styled } from '@mui/material/styles';
import { translations, TranslationKeys } from '../utils/translations';

const ChatContainer = styled(Box)(({ theme }: { theme: Theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
}));

const MessagesContainer = styled(Box)(({ theme }: { theme: Theme }) => ({
  flex: 1,
  overflowY: 'auto',
  padding: theme.spacing(2),
  background: 'linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%)',
  '&::-webkit-scrollbar': {
    width: '8px',
  },
  '&::-webkit-scrollbar-track': {
    background: 'transparent',
  },
  '&::-webkit-scrollbar-thumb': {
    background: '#cbd5e0',
    borderRadius: '4px',
  },
}));

const MessageBubble = styled(Paper, {
  shouldForwardProp: (prop: string) => prop !== 'isUser',
})<{ isUser?: boolean }>(({ theme, isUser }: { theme: Theme; isUser?: boolean }) => ({
  padding: theme.spacing(2),
  marginBottom: theme.spacing(2),
  borderRadius: isUser ? '20px 20px 4px 20px' : '20px 20px 20px 4px',
  maxWidth: '70%',
  alignSelf: isUser ? 'flex-end' : 'flex-start',
  background: isUser
    ? 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
    : '#ffffff',
  color: isUser ? '#ffffff' : '#2d3748',
  boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
  animation: 'slideIn 0.3s ease-out',
  '@keyframes slideIn': {
    from: {
      opacity: 0,
      transform: 'translateY(10px)',
    },
    to: {
      opacity: 1,
      transform: 'translateY(0)',
    },
  },
}));

const InputContainer = styled(Box)(({ theme }: { theme: Theme }) => ({
  padding: theme.spacing(2),
  background: '#ffffff',
  borderTop: '1px solid #e2e8f0',
  display: 'flex',
  gap: theme.spacing(2),
  alignItems: 'flex-end',
}));

const StyledTextField = styled(TextField)(({ theme }: { theme: Theme }) => ({
  '& .MuiOutlinedInput-root': {
    borderRadius: 25,
    background: '#f8fafc',
    '&:hover': {
      background: '#f1f5f9',
    },
    '&.Mui-focused': {
      background: '#ffffff',
    },
  },
}));

const SendButton = styled(IconButton)(({ theme }: { theme: Theme }) => ({
  background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
  color: '#ffffff',
  width: 48,
  height: 48,
  '&:hover': {
    background: 'linear-gradient(135deg, #5a67d8 0%, #6b46c1 100%)',
    transform: 'scale(1.05)',
  },
  '&:disabled': {
    background: '#e2e8f0',
    color: '#a0aec0',
  },
}));

interface Message {
  id: string;
  role: 'user' | 'assistant';
  content: string;
  timestamp: Date;
  isAudio?: boolean;
}

interface ChatInterfaceProps {
  messages: Message[];
  onSendMessage: (message: string, isAudio?: boolean) => void;
  isLoading: boolean;
  language: 'arabic' | 'english';
}

const ChatInterface: React.FC<ChatInterfaceProps> = ({
  messages,
  onSendMessage,
  isLoading,
  language,
}) => {
  const [inputMessage, setInputMessage] = useState('');
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const t: TranslationKeys = translations[language];

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const handleSendMessage = () => {
    if (inputMessage.trim()) {
      onSendMessage(inputMessage.trim());
      setInputMessage('');
    }
  };

  const handleKeyPress = (event: React.KeyboardEvent) => {
    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault();
      handleSendMessage();
    }
  };

  const formatTimestamp = (timestamp: Date) => {
    return timestamp.toLocaleTimeString(language === 'arabic' ? 'ar-OM' : 'en-US', {
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  return (
    <ChatContainer>
      <Box sx={{ p: 2, background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)', color: 'white' }}>
        <Typography variant="h6" sx={{ fontWeight: 600 }}>
          💬 {t.chat_interface || 'Chat Interface'}
        </Typography>
      </Box>

      <MessagesContainer>
        {messages.length === 0 ? (
          <Box textAlign="center" sx={{ mt: 4 }}>
            <SmartToy sx={{ fontSize: 64, color: '#cbd5e0', mb: 2 }} />
            <Typography variant="h6" color="textSecondary" gutterBottom>
              {t.welcome_message || 'Welcome to OMANI Therapist'}
            </Typography>
            <Typography variant="body2" color="textSecondary">
              {t.start_conversation || 'Start a conversation by typing a message or using voice input'}
            </Typography>
          </Box>
        ) : (
          <>
            {messages.map((message: Message, index: number) => (
              <Fade key={message.id} in timeout={300}>
                <Box
                  sx={{
                    display: 'flex',
                    justifyContent: message.role === 'user' ? 'flex-end' : 'flex-start',
                    mb: 1,
                  }}
                >
                  <Box
                    sx={{
                      display: 'flex',
                      alignItems: 'flex-start',
                      gap: 1,
                      maxWidth: '80%',
                      flexDirection: message.role === 'user' ? 'row-reverse' : 'row',
                    }}
                  >
                    <Avatar
                      sx={{
                        bgcolor: message.role === 'user' ? '#667eea' : '#10b981',
                        width: 36,
                        height: 36,
                        mt: 1,
                      }}
                    >
                      {message.role === 'user' ? <Person /> : <SmartToy />}
                    </Avatar>
                    <Box>
                      <MessageBubble isUser={message.role === 'user'}>
                        <Typography variant="body1" sx={{ whiteSpace: 'pre-wrap' }}>
                          {message.content}
                        </Typography>
                        {message.isAudio && (
                          <Chip
                            icon={<VolumeUp />}
                            label={t.audio_message || 'Audio Message'}
                            size="small"
                            sx={{ mt: 1, opacity: 0.8 }}
                          />
                        )}
                      </MessageBubble>
                      <Typography
                        variant="caption"
                        color="textSecondary"
                        sx={{
                          ml: message.role === 'user' ? 0 : 1,
                          mr: message.role === 'user' ? 1 : 0,
                          textAlign: message.role === 'user' ? 'right' : 'left',
                        }}
                      >
                        {formatTimestamp(message.timestamp)}
                      </Typography>
                    </Box>
                  </Box>
                </Box>
              </Fade>
            ))}
            {isLoading && (
              <Box sx={{ display: 'flex', justifyContent: 'flex-start', mb: 2 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                  <Avatar sx={{ bgcolor: '#10b981', width: 36, height: 36 }}>
                    <SmartToy />
                  </Avatar>
                  <MessageBubble>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <CircularProgress size={16} />
                      <Typography variant="body2">
                        {t.thinking || 'Thinking...'}
                      </Typography>
                    </Box>
                  </MessageBubble>
                </Box>
              </Box>
            )}
            <div ref={messagesEndRef} />
          </>
        )}
      </MessagesContainer>

      <InputContainer>
        <StyledTextField
          fullWidth
          multiline
          maxRows={4}
          value={inputMessage}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => setInputMessage(e.target.value)}
          onKeyPress={handleKeyPress}
          placeholder={t.chat_placeholder || 'Type your message here...'}
          disabled={isLoading}
          sx={{
            '& .MuiInputBase-root': {
              fontSize: '1rem',
              direction: language === 'arabic' ? 'rtl' : 'ltr',
            },
          }}
        />
        <SendButton
          onClick={handleSendMessage}
          disabled={!inputMessage.trim() || isLoading}
        >
          <Send />
        </SendButton>
      </InputContainer>
    </ChatContainer>
  );
};

export default ChatInterface;
