import React from 'react';
import {
  Box,
  Typography,
  Alert,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
} from '@mui/material';
import {
  ReportProblem,
  LocalHospital,
  Phone,
  AccessTime,
} from '@mui/icons-material';
import { translations, TranslationKeys } from '../utils/translations';

interface EmergencyContactsProps {
  language: 'arabic' | 'english';
}

const EmergencyContacts: React.FC<EmergencyContactsProps> = ({ language }) => {
  const t: TranslationKeys = translations[language];

  const contacts = [
    {
      icon: <ReportProblem />,
      primary: t.general_emergency || 'General Emergency',
      secondary: '999',
      description: t.emergency_desc || 'For immediate emergency assistance',
    },
    {
      icon: <LocalHospital />,
      primary: t.sultan_hospital || 'Sultan Qaboos Hospital',
      secondary: '24211411',
      description: t.hospital_desc || 'Main hospital in Muscat',
    },
    {
      icon: <Phone />,
      primary: t.mental_health_hotline || 'Mental Health Hotline',
      secondary: t.available_24_7 || 'Available 24/7',
      description: t.hotline_desc || 'Professional mental health support',
    },
  ];

  return (
    <Box>
      <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
        📞 {t.emergency_contacts || 'Emergency Contacts'}
      </Typography>
      
      <Alert severity="warning" sx={{ mb: 2, borderRadius: 2 }}>
        <Typography variant="body2" sx={{ fontWeight: 500 }}>
          🆘 {t.emergency_alert || 'In case of emergency, call immediately'}
        </Typography>
      </Alert>

      <List dense>
        {contacts.map((contact, index) => (
          <React.Fragment key={index}>
            <ListItem sx={{ px: 0 }}>
              <ListItemIcon sx={{ minWidth: 36 }}>
                {contact.icon}
              </ListItemIcon>
              <ListItemText
                primary={
                  <Typography variant="body2" sx={{ fontWeight: 500 }}>
                    {contact.primary}
                  </Typography>
                }
                secondary={
                  <Box>
                    <Typography variant="body2" color="primary" sx={{ fontWeight: 600 }}>
                      {contact.secondary}
                    </Typography>
                    <Typography variant="caption" color="textSecondary">
                      {contact.description}
                    </Typography>
                  </Box>
                }
              />
            </ListItem>
            {index < contacts.length - 1 && <Divider />}
          </React.Fragment>
        ))}
      </List>

      <Box sx={{ mt: 2, p: 2, bgcolor: 'background.default', borderRadius: 2 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
          <AccessTime sx={{ mr: 1, fontSize: 18, color: 'text.secondary' }} />
          <Typography variant="body2" sx={{ fontWeight: 500 }}>
            {t.crisis_support || 'Crisis Support'}
          </Typography>
        </Box>
        <Typography variant="body2" color="textSecondary">
          {t.crisis_message || 'If you are in crisis, please reach out immediately. Help is available 24/7.'}
        </Typography>
      </Box>
    </Box>
  );
};

export default EmergencyContacts;
