import React, { useState, useRef } from 'react';
import {
  Box,
  Typography,
  Button,
  IconButton,
  Paper,
  LinearProgress,
  Alert,
  Chip,
  Divider,
} from '@mui/material';
import {
  Mic,
  Stop,
  CloudUpload,
  VolumeUp,
  Delete,
} from '@mui/icons-material';
import { styled } from '@mui/material/styles';
import { translations, TranslationKeys } from '../utils/translations';

const VoiceContainer = styled(Box)(({ theme }) => ({
  padding: theme.spacing(2),
}));

const RecordButton = styled(IconButton, {
  shouldForwardProp: (prop) => prop !== 'isRecording',
})<{ isRecording?: boolean }>(({ theme, isRecording }) => ({
  width: 80,
  height: 80,
  background: isRecording
    ? 'linear-gradient(135deg, #ef4444 0%, #dc2626 100%)'
    : 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
  color: '#ffffff',
  margin: theme.spacing(2),
  '&:hover': {
    background: isRecording
      ? 'linear-gradient(135deg, #dc2626 0%, #b91c1c 100%)'
      : 'linear-gradient(135deg, #5a67d8 0%, #6b46c1 100%)',
    transform: 'scale(1.05)',
  },
  animation: isRecording ? 'pulse 2s infinite' : 'none',
  '@keyframes pulse': {
    '0%': {
      transform: 'scale(1)',
      boxShadow: '0 0 0 0 rgba(239, 68, 68, 0.7)',
    },
    '70%': {
      transform: 'scale(1.05)',
      boxShadow: '0 0 0 10px rgba(239, 68, 68, 0)',
    },
    '100%': {
      transform: 'scale(1)',
      boxShadow: '0 0 0 0 rgba(239, 68, 68, 0)',
    },
  },
}));

const UploadArea = styled(Paper)(({ theme }) => ({
  border: '2px dashed #cbd5e0',
  borderRadius: theme.spacing(2),
  padding: theme.spacing(4),
  textAlign: 'center',
  cursor: 'pointer',
  transition: 'all 0.3s ease',
  '&:hover': {
    borderColor: '#667eea',
    backgroundColor: '#f8fafc',
  },
  '&.dragover': {
    borderColor: '#667eea',
    backgroundColor: '#edf2f7',
  },
}));

interface VoiceRecorderProps {
  onAudioUpload: (file: File) => void;
  onRecordComplete: (transcript: string, isAudio: boolean) => void;
  language: 'arabic' | 'english';
}

const VoiceRecorder: React.FC<VoiceRecorderProps> = ({
  onAudioUpload,
  onRecordComplete,
  language,
}) => {
  const [isRecording, setIsRecording] = useState(false);
  const [audioBlob, setAudioBlob] = useState<Blob | null>(null);
  const [recordingTime, setRecordingTime] = useState(0);
  const [isProcessing, setIsProcessing] = useState(false);
  const [uploadedFile, setUploadedFile] = useState<File | null>(null);
  const [dragOver, setDragOver] = useState(false);
  
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  const t: TranslationKeys = translations[language];

  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const mediaRecorder = new MediaRecorder(stream);
      const chunks: Blob[] = [];

      mediaRecorder.ondataavailable = (event) => {
        chunks.push(event.data);
      };

      mediaRecorder.onstop = () => {
        const blob = new Blob(chunks, { type: 'audio/wav' });
        setAudioBlob(blob);
        stream.getTracks().forEach(track => track.stop());
      };

      mediaRecorder.start();
      mediaRecorderRef.current = mediaRecorder;
      setIsRecording(true);
      setRecordingTime(0);

      intervalRef.current = setInterval(() => {
        setRecordingTime(prev => prev + 1);
      }, 1000);
    } catch (error) {
      console.error('Error accessing microphone:', error);
      alert(t.microphone_error || 'Error accessing microphone');
    }
  };

  const stopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
      
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    }
  };

  const processAudio = async () => {
    if (!audioBlob) return;

    setIsProcessing(true);
    try {
      // Simulate audio processing
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // Simulate transcription
      const mockTranscript = language === 'arabic' 
        ? 'مرحبا، أشعر بالحزن اليوم وأحتاج إلى التحدث مع شخص ما'
        : 'Hello, I am feeling sad today and need to talk to someone';
      
      onRecordComplete(mockTranscript, true);
      setAudioBlob(null);
      setRecordingTime(0);
    } catch (error) {
      console.error('Error processing audio:', error);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleFileUpload = (file: File) => {
    if (file.type.startsWith('audio/')) {
      setUploadedFile(file);
      onAudioUpload(file);
    } else {
      alert(t.invalid_file_type || 'Please upload an audio file');
    }
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setDragOver(true);
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    setDragOver(false);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setDragOver(false);
    
    const files = Array.from(e.dataTransfer.files);
    if (files.length > 0) {
      handleFileUpload(files[0]);
    }
  };

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <VoiceContainer>
      <Typography variant="h6" gutterBottom sx={{ textAlign: 'center', fontWeight: 600 }}>
        🎤 {t.voice_recorder || 'Voice Recorder'}
      </Typography>

      <Box sx={{ textAlign: 'center', mb: 3 }}>
        <RecordButton
          isRecording={isRecording}
          onClick={isRecording ? stopRecording : startRecording}
          disabled={isProcessing}
        >
          {isRecording ? <Stop /> : <Mic />}
        </RecordButton>
        
        {isRecording && (
          <Box sx={{ mt: 2 }}>
            <Chip
              icon={<VolumeUp />}
              label={`${t.recording || 'Recording'} ${formatTime(recordingTime)}`}
              color="error"
              sx={{ animation: 'pulse 1s infinite' }}
            />
          </Box>
        )}
      </Box>

      {audioBlob && (
        <Box sx={{ mb: 3 }}>
          <Alert severity="success" sx={{ mb: 2 }}>
            {t.recording_complete || 'Recording complete!'}
          </Alert>
          <audio controls src={URL.createObjectURL(audioBlob)} style={{ width: '100%' }} />
          <Box sx={{ mt: 2, display: 'flex', gap: 1 }}>
            <Button
              variant="contained"
              onClick={processAudio}
              disabled={isProcessing}
              fullWidth
            >
              {isProcessing ? t.processing || 'Processing...' : t.process_audio || 'Process Audio'}
            </Button>
            <IconButton onClick={() => setAudioBlob(null)} color="error">
              <Delete />
            </IconButton>
          </Box>
        </Box>
      )}

      {isProcessing && (
        <Box sx={{ mb: 3 }}>
          <LinearProgress sx={{ borderRadius: 2, height: 8 }} />
          <Typography variant="body2" textAlign="center" sx={{ mt: 1 }}>
            {t.processing_audio || 'Processing audio...'}
          </Typography>
        </Box>
      )}

      <Divider sx={{ my: 3 }} />

      <UploadArea
        className={dragOver ? 'dragover' : ''}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        onClick={() => fileInputRef.current?.click()}
      >
        <CloudUpload sx={{ fontSize: 48, color: '#cbd5e0', mb: 2 }} />
        <Typography variant="body1" gutterBottom>
          {t.upload_audio || 'Upload Audio File'}
        </Typography>
        <Typography variant="body2" color="textSecondary">
          {t.drag_drop_audio || 'Drag and drop an audio file here, or click to select'}
        </Typography>
        <Typography variant="caption" color="textSecondary" sx={{ mt: 1, display: 'block' }}>
          {t.supported_formats || 'Supported formats: MP3, WAV, OGG, M4A'}
        </Typography>
      </UploadArea>

      <input
        ref={fileInputRef}
        type="file"
        accept="audio/*"
        style={{ display: 'none' }}
        onChange={(e) => {
          const file = e.target.files?.[0];
          if (file) handleFileUpload(file);
        }}
      />

      {uploadedFile && (
        <Box sx={{ mt: 2 }}>
          <Alert severity="info">
            {t.uploaded_file || 'Uploaded file:'} {uploadedFile.name}
          </Alert>
        </Box>
      )}
    </VoiceContainer>
  );
};

export default VoiceRecorder;
