import React from 'react';
import {
  Box,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Chip,
  Divider,
} from '@mui/material';
import {
  RecordVoiceOver,
  Psychology,
  Language,
  Security,
  CheckCircle,
  Error,
} from '@mui/icons-material';
import { translations, TranslationKeys } from '../utils/translations';

interface SystemStatusProps {
  sttReady: boolean;
  llmReady: boolean;
  language: 'arabic' | 'english';
}

const SystemStatus: React.FC<SystemStatusProps> = ({
  sttReady,
  llmReady,
  language,
}) => {
  const t: TranslationKeys = translations[language];

  const statusItems = [
    {
      icon: <RecordVoiceOver />,
      primary: t.stt_system || 'Speech Recognition',
      secondary: 'Groq Whisper',
      status: sttReady,
    },
    {
      icon: <Psychology />,
      primary: t.llm_system || 'AI System',
      secondary: 'Azure OpenAI GPT-4o',
      status: llmReady,
    },
    {
      icon: <Language />,
      primary: t.language_label || 'Language',
      secondary: language === 'arabic' ? 'العربية العمانية' : 'Omani Arabic + English',
      status: true,
    },
    {
      icon: <Security />,
      primary: t.safety_label || 'Safety',
      secondary: t.safety_value || 'Islamic values & Omani culture',
      status: true,
    },
  ];

  return (
    <Box>
      <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
        📊 {t.system_status || 'System Status'}
      </Typography>
      
      <List dense>
        {statusItems.map((item, index) => (
          <React.Fragment key={index}>
            <ListItem sx={{ px: 0 }}>
              <ListItemIcon sx={{ minWidth: 36 }}>
                {item.icon}
              </ListItemIcon>
              <ListItemText
                primary={
                  <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <Typography variant="body2" sx={{ fontWeight: 500 }}>
                      {item.primary}
                    </Typography>
                    <Chip
                      icon={item.status ? <CheckCircle /> : <Error />}
                      label={item.status ? 'Ready' : 'Error'}
                      size="small"
                      color={item.status ? 'success' : 'error'}
                      sx={{ height: 24 }}
                    />
                  </Box>
                }
                secondary={
                  <Typography variant="caption" color="textSecondary">
                    {item.secondary}
                  </Typography>
                }
              />
            </ListItem>
            {index < statusItems.length - 1 && <Divider />}
          </React.Fragment>
        ))}
      </List>
    </Box>
  );
};

export default SystemStatus;
