import React, { useState, useEffect } from 'react';
import { Theme } from '@mui/material/styles';
import {
  Box,
  Container,
  Grid,
  Paper,
  Typography,
  Button,
  TextField,
  Card,
  CardContent,
  IconButton,
  Chip,
  Alert,
  CircularProgress,
  Fade,
  Divider,
  Avatar,
  AppBar,
  Toolbar,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
} from '@mui/material';
import {
  Send,
  Mic,
  CloudUpload,
  Psychology,
  Language,
  Clear,
  Person,
  SmartToy,
  VolumeUp,
  Settings,
  Info,
} from '@mui/icons-material';
import { styled } from '@mui/material/styles';
import ChatInterface from './components/ChatInterface';
import VoiceRecorder from './components/VoiceRecorder';
import SystemStatus from './components/SystemStatus';
import EmergencyContacts from './components/EmergencyContacts';
import LanguageSelector from './components/LanguageSelector';
import { translations, TranslationKeys } from './utils/translations';
import { apiService } from './services/apiService';

const StyledAppBar = styled(AppBar)(({ theme }: { theme: Theme }) => ({
  background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
  boxShadow: '0 8px 32px rgba(0, 0, 0, 0.12)',
  backdropFilter: 'blur(20px)',
}));

const MainContainer = styled(Container)(({ theme }: { theme: Theme }) => ({
  minHeight: '100vh',
  paddingTop: theme.spacing(3),
  paddingBottom: theme.spacing(3),
  background: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
}));

const WelcomeCard = styled(Card)(({ theme }: { theme: Theme }) => ({
  background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
  color: 'white',
  marginBottom: theme.spacing(3),
  borderRadius: 20,
  boxShadow: '0 20px 40px rgba(0, 0, 0, 0.1)',
}));

const ChatCard = styled(Card)(({ theme }: { theme: Theme }) => ({
  height: '600px',
  display: 'flex',
  flexDirection: 'column',
  borderRadius: 20,
  boxShadow: '0 20px 40px rgba(0, 0, 0, 0.1)',
  overflow: 'hidden',
}));

const SidebarCard = styled(Card)(({ theme }: { theme: Theme }) => ({
  borderRadius: 20,
  boxShadow: '0 20px 40px rgba(0, 0, 0, 0.1)',
  padding: theme.spacing(2),
}));

interface Message {
  id: string;
  role: 'user' | 'assistant';
  content: string;
  timestamp: Date;
  isAudio?: boolean;
}

interface SystemState {
  sttReady: boolean;
  llmReady: boolean;
  connecting: boolean;
  error: string | null;
}

const App: React.FC = () => {
  const [language, setLanguage] = useState<'arabic' | 'english'>('arabic');
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [systemState, setSystemState] = useState<SystemState>({
    sttReady: false,
    llmReady: false,
    connecting: true,
    error: null,
  });

  const t: TranslationKeys = translations[language];

  useEffect(() => {
    // Initialize system components
    initializeSystem();
  }, []);

  const initializeSystem = async () => {
    try {
      setSystemState((prev: SystemState) => ({ ...prev, connecting: true, error: null }));
      
      // Simulate system initialization
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      setSystemState({
        sttReady: true,
        llmReady: true,
        connecting: false,
        error: null,
      });
    } catch (error) {
      setSystemState({
        sttReady: false,
        llmReady: false,
        connecting: false,
        error: 'Failed to initialize system components',
      });
    }
  };

  const handleSendMessage = async (content: string, isAudio = false) => {
    if (!content.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      role: 'user',
      content: isAudio ? `🎤 ${content}` : content,
      timestamp: new Date(),
      isAudio,
    };

    setMessages((prev: Message[]) => [...prev, userMessage]);
    setIsLoading(true);

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: generateResponse(content),
        timestamp: new Date(),
      };

      setMessages((prev: Message[]) => [...prev, assistantMessage]);
    } catch (error) {
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: t.error_prefix + ' ' + (error instanceof Error ? error.message : 'Unknown error'),
        timestamp: new Date(),
      };
      setMessages((prev: Message[]) => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const generateResponse = (userMessage: string): string => {
    // Simple response generation for demo
    const responses = [
      t.thinking_response || 'I understand how you feel. Let me help you with that.',
      'Thank you for sharing. Your feelings are valid and important.',
      'I hear you. Would you like to talk more about what is causing you distress?',
      'It takes courage to reach out. I am here to support you.',
      'Your mental health matters. Let us work through this together.',
    ];
    
    return responses[Math.floor(Math.random() * responses.length)];
  };

  const handleClearChat = () => {
    setMessages([]);
  };

  const handleLanguageChange = (newLanguage: 'arabic' | 'english') => {
    setLanguage(newLanguage);
  };

  const handleAudioUpload = (file: File) => {
    // Simulate audio processing
    const reader = new FileReader();
    reader.onload = () => {
      // Simulate transcription
      setTimeout(() => {
        handleSendMessage('This is a transcribed message from audio', true);
      }, 2000);
    };
    reader.readAsDataURL(file);
  };

  if (systemState.connecting) {
    return (
      <Box 
        display="flex" 
        justifyContent="center" 
        alignItems="center" 
        minHeight="100vh"
        flexDirection="column"
        gap={2}
      >
        <CircularProgress size={60} />
        <Typography variant="h6" color="textSecondary">
          {t.initializing}
        </Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ minHeight: '100vh' }}>
      <StyledAppBar position="static" elevation={0}>
        <Toolbar>
          <Psychology sx={{ mr: 2 }} />
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            {t.title}
          </Typography>
          <LanguageSelector 
            language={language} 
            onLanguageChange={handleLanguageChange} 
          />
        </Toolbar>
      </StyledAppBar>

      <MainContainer maxWidth="xl">
        <Fade in timeout={1000}>
          <WelcomeCard>
            <CardContent sx={{ textAlign: 'center', py: 4 }}>
              <Typography variant="h4" gutterBottom sx={{ fontWeight: 700 }}>
                {t.title}
              </Typography>
              <Typography variant="h6" sx={{ opacity: 0.9 }}>
                {t.subtitle}
              </Typography>
            </CardContent>
          </WelcomeCard>
        </Fade>

        {systemState.error && (
          <Alert severity="error" sx={{ mb: 3, borderRadius: 2 }}>
            {systemState.error}
          </Alert>
        )}

        <Grid container spacing={3}>
          <Grid item xs={12} lg={8}>
            <ChatCard>
              <ChatInterface
                messages={messages}
                onSendMessage={handleSendMessage}
                isLoading={isLoading}
                language={language}
              />
            </ChatCard>
          </Grid>

          <Grid item xs={12} lg={4}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <SidebarCard>
                  <VoiceRecorder
                    onAudioUpload={handleAudioUpload}
                    onRecordComplete={handleSendMessage}
                    language={language}
                  />
                </SidebarCard>
              </Grid>

              <Grid item xs={12}>
                <SidebarCard>
                  <SystemStatus
                    sttReady={systemState.sttReady}
                    llmReady={systemState.llmReady}
                    language={language}
                  />
                </SidebarCard>
              </Grid>

              <Grid item xs={12}>
                <SidebarCard>
                  <EmergencyContacts language={language} />
                </SidebarCard>
              </Grid>

              <Grid item xs={12}>
                <SidebarCard>
                  <Box textAlign="center">
                    <Button
                      variant="outlined"
                      startIcon={<Clear />}
                      onClick={handleClearChat}
                      fullWidth
                      sx={{ mb: 2 }}
                    >
                      {t.clear_chat}
                    </Button>
                    
                    <Divider sx={{ my: 2 }} />
                    
                    <Typography variant="body2" color="textSecondary">
                      <strong>Version:</strong> 1.0.0
                    </Typography>
                    <Typography variant="body2" color="textSecondary">
                      <strong>Build:</strong> React + Azure OpenAI + Groq
                    </Typography>
                  </Box>
                </SidebarCard>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </MainContainer>
    </Box>
  );
};

export default App;
